//vuevuex
import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const store = new Vuex.Store({//
    state: {
       isSHowAddNft:false,
	   tabhHeight:0,//
	   tabh:0,//
    },
    mutations: {
      changeAddNft(state,value){
		  state.isSHowAddNft=value
	  },
	  changetabhHeight(state,value){
	  		  state.tabhHeight=value
	  },
	  changetabh(state,value){
	  		  state.tabh=value
	  },
    },
	
})
export default store